<template>
  <div class="container" style="margin-top: 80px;">
    <div class="row">
      <div class="col-md-12 order-md-1">
        <form class="form-signin"  method="post">
          <img
            class="mb-4"
            src="../../assets/images/bootstrap-solid.svg"
            alt
            width="72"
            height="72"
          />
          <h1 class="h3 mb-3 font-weight-normal"></h1>
          <!---->
          <label class="sr-only"></label>
          <input
            type="text"
            name="userName"
            class="form-control"
            placeholder=""
            required
            autofocus
            v-model="userName"
          />
          <label class="sr-only"></label>
          <input
            type="password"
            name="password"
            class="form-control"
            placeholder=""
            required
            v-model="password"
          />
          <div class="checkbox mb-3">
            <label>
              <input type="checkbox" name="remember" />
            </label>
          </div>
          <button class="btn btn-lg btn-primary btn-block" type="button" @click="login"></button>
          <router-link class="btn btn-lg btn-primary btn-block" to="/register"></router-link>
          <!--<a class="btn btn-sm" th:href="@{/index.html(l='zh_CN')}"></a>-->
          <!--<a class="btn btn-sm" th:href="@{/index.html(l='en_US')}">English</a>-->
        </form>
      </div>
    </div>
    <!--footer-->
    <footer th:replace="commons/bar::footer"></footer>
  </div>
</template>
<script>
// /user/login
// import request from "../../api/ajax";
import axios from 'axios'
export default {
  data() {
    return {
      userName: "",
      password: ""
    };
  },
  methods: {
    login() {
      const username = this.userName;
      const password = this.password;
      console.log(username + this.password);
      axios({
          url: "/user/login",
          method: "post",
              // res.header('Access-Control-Allow-Headers', 'Content-Type, Content-Length, Authorization, Accept, X-Requested-With , yourHeaderFeild');
          // JSON
          data: {
            username,
            password
          }
        })
        .then(res => {
          console.log(res);
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
<style>
html,
container {
  height: 100%;
}

container {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-align: center;
  -ms-flex-pack: center;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
  /*background-color: #f5f5f5;*/
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}

.form-signin .checkbox {
  font-weight: 400;
}

.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}

.form-signin .form-control:focus {
  z-index: 2;
}

.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>